body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}.App-header{background-color:#b040be;height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:20px;color:white}.App-header nav{width:100%}.App-header nav ul{list-style-type:none;margin:0;padding:0}.App-header nav ul li{display:inline-block;padding:.5rem 2rem;position:relative;background:#ccc;margin-left:25px}.App-header nav ul li a{color:#b040be;text-decoration:none}.App-header nav ul li a:after{bottom:0;content:'';left:0;position:absolute;right:0;top:0}.App-header nav ul li a.here,.App-header nav ul li a.here:hover{color:red}.App-header nav ul li:hover{background:#111}.App-header nav ul li:hover a{color:#f5f5f5}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}.App-footer{background-color:#a19e99;height:8vh;width:100%;bottom:0;color:#b040be;display:flex;align-items:center;justify-content:center;position:absolute}.App-footer p{text-align:left;width:100%;padding-left:50px;font-weight:700;font-size:12px}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%}.form{margin:70px 0;display:flex;flex-direction:row;width:100%;height:40px;justify-content:space-around;align-items:center}.form fieldset{width:30%;height:100%;padding:20px;align-items:center;border-radius:.3em}.form input{height:30px;width:60%}.form button{margin-left:20px;height:90%;align-self:center;width:70px;background:#d9e4dd;color:#b040be;border:none}.form textarea{width:400px;margin-left:30px}.Show{height:80px;width:95%;display:flex;margin:auto;justify-content:center;align-items:center;border:1px solid black}.Show span{margin:5px}.result{margin-bottom:10vh;max-height:40vh;overflow:scroll;border:1px solid #a19e99;padding:10px;margin:20px}

#overlay{top:0;left:0;right:0;bottom:0;position:absolute;display:block;background:rgba(0,0,0,0.5)}#overlay div{top:30%;position:relative;text-align:center}#overlay p{display:inline-block;text-transform:uppercase;text-align:center;font-size:4em;font-family:arial;font-weight:600;transform:scale(0.5);color:#121212;-webkit-text-stroke:2px gray}#overlay p:nth-child(1){-webkit-animation:var(--effect);animation:var(--effect)}#overlay p:nth-child(2){-webkit-animation:var(--effect) 0.125s;animation:var(--effect) 0.125s}#overlay p:nth-child(3){-webkit-animation:var(--effect) 0.25s;animation:var(--effect) 0.25s}#overlay p:nth-child(4){-webkit-animation:var(--effect) 0.375s;animation:var(--effect) 0.375s}#overlay p:nth-child(5){-webkit-animation:var(--effect) 0.5s;animation:var(--effect) 0.5s}#overlay p:nth-child(6){-webkit-animation:var(--effect) 0.675s;animation:var(--effect) 0.675s}#overlay p:nth-child(7){-webkit-animation:var(--effect) 0.75s;animation:var(--effect) 0.75s}@-webkit-keyframes hover{0%{transform:scale(0.5);color:#121212;-webkit-text-stroke:2px gray}20%{transform:scale(1);color:pink;-webkit-text-stroke:3px red;-webkit-filter:drop-shadow(0 0 1px black) drop-shadow(0 0 1px black) drop-shadow(0 0 3px red) drop-shadow(0 0 5px red) hue-rotate(10turn);filter:drop-shadow(0 0 1px black) drop-shadow(0 0 1px black) drop-shadow(0 0 3px red) drop-shadow(0 0 5px red) hue-rotate(10turn)}50%{transform:scale(0.5);color:#121212;-webkit-text-stroke:2px gray}}@keyframes hover{0%{transform:scale(0.5);color:#121212;-webkit-text-stroke:2px gray}20%{transform:scale(1);color:pink;-webkit-text-stroke:3px red;-webkit-filter:drop-shadow(0 0 1px black) drop-shadow(0 0 1px black) drop-shadow(0 0 3px red) drop-shadow(0 0 5px red) hue-rotate(10turn);filter:drop-shadow(0 0 1px black) drop-shadow(0 0 1px black) drop-shadow(0 0 3px red) drop-shadow(0 0 5px red) hue-rotate(10turn)}50%{transform:scale(0.5);color:#121212;-webkit-text-stroke:2px gray}}.ResultApp{border:solid 2px #635a88;height:300px;min-height:20vmin;border-radius:.3em;padding:1em;overflow:scroll;margin-bottom:10vh}

.App-history{border:solid 2px #635a88;margin:.5em;min-height:20vmin;border-radius:.3em}.App-history button{background-color:#b0a8b1;margin:3px;border-radius:.3em}.App-history h3{margin:1em}

